  ngOnInit() {
 // Set 10 seconds interval to update data again and again
 const source = interval(100000);

 // Sample API
 const apiLink = 'https://localhost:44392/api/student';

 this.subscription = source.subscribe(val => this.getApiResponse(apiLink).then(
  data => {
    const updated_normal_data = [];
    const updated_abnormal_data = [];
    data.forEach(row => {
      const temp_row = [
        row.name,
        row.id,
        row.english,
        row.maths,
        row.science,
        row.social_science
      ];
      updated_normal_data.push(temp_row);
      debugger;
    });
    this.options.series[0]['data'] = updated_normal_data;
    
    Highcharts.chart('container', this.options);
  },
  error => {
    console.log('Something went wrong.');
  })
);
}

getApiResponse(url) {
return this.studentservice.Get(this.url)
  .toPromise().then(res => {
    return res;
  });
}

